@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins',sans-serif;
}

body{
    width: 100%;
    min-height: 100vh;
    background-image: url(/weatherApp/static/media/ottawa2.3dd9f202.jpeg);
    background-size: cover;
    background-repeat: repeat-y;
    background-position: center;
    overflow-y: auto;

}
#root{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.container{
    width: 80%;
    height: 40%;
    position: relative;
    bottom: 50px;
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-template-rows: 3fr 180px;
    grid-gap: 5px;
}

.week-weather{
    grid-column: 1 / 3;
    background-color:rgb(37, 37, 37);
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 20px;
}
.line{
    display: inline-block;
    width:2px;
    height: 150px;
    background: #00000020;
}

.input-search{
    position: absolute;
    top: 12%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: 40px;
    border-radius: 20px;
    padding: 0 10px;
    outline: none;
    border: 1px solid rgba(255, 255, 255, 0.26);
    background: transparent;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    color: #fff;
    -webkit-appearance: none;
    appearance: none;
    transition: all 0.3s ease-in-out;
}
.input-search::-webkit-search-cancel-button{
    -webkit-appearance: none;
    appearance: none;
}
.input-search:focus{
    border:1px solid #fff;
}
.input-search:focus::-webkit-input-placeholder{
    letter-spacing: 3px;
}
.input-search:focus:-ms-input-placeholder{
    letter-spacing: 3px;
}
.input-search:focus::placeholder{
    letter-spacing: 3px;
}
.input-search::-webkit-input-placeholder{
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.input-search:-ms-input-placeholder{
    -ms-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.input-search::placeholder{
    transition: all 0.3s ease-in-out;
}

.lg-icon{
    display: block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}

.icon-01d{
    background: url(/weatherApp/static/media/wi-day-sunny.aa52ce10.svg);
}
.icon-01n{
    background: url(/weatherApp/static/media/wi-moon-waning-crescent-5.6769cb35.svg);
}
.icon-02d{
    background: url(/weatherApp/static/media/wi-day-cloudy.233c39ed.svg);
}
.icon-02n{
    background: url(/weatherApp/static/media/wi-night-alt-cloudy.1bcc4222.svg);
}
.icon-03d{
    background: url(/weatherApp/static/media/wi-cloud.d9190414.svg);
}
.icon-03n{
    background: url(/weatherApp/static/media/wi-cloud.d9190414.svg);
}
.icon-04d{
    background: url(/weatherApp/static/media/wi-smoke.ee3d4b82.svg);
}
.icon-04n{
    background: url(/weatherApp/static/media/wi-smoke.ee3d4b82.svg);
}
.icon-09d{
    background: url(/weatherApp/static/media/wi-showers.6dae08c5.svg);
}
.icon-09n{
    background: url(/weatherApp/static/media/wi-showers.6dae08c5.svg);
}
.icon-10d{
    background: url(/weatherApp/static/media/wi-day-showers.aa3eff22.svg);
}
.icon-10n{
    background: url(/weatherApp/static/media/wi-night-alt-showers.42c5b658.svg);
}
.icon-11d{
