}

a{
    display: block;
    position: absolute;
    left: 30px;
    top: 10px;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.685);
    font-size: 1.1em;
    letter-spacing: 1px;
    text-transform: uppercase;
    border: 1px solid rgba(255, 255, 255, 0.247);
    padding: 10px;
}

a:hover{
    color: #fff;
    background-color: rgba(255, 255, 255, 0.192);
}

@media screen and (max-width: 801px){
    .container{
        width: 100%;
        height: 40%;
        grid-template-columns: 1fr 150px;
        grid-template-rows: 3fr 180px;
        grid-gap: 5px;
    }
    .container .city-information{
        justify-content: space-around;
        align-items: center;
    }
    .day-weather-information{
        grid-column: 1 / 2;
        grid-row: 2 / 3;
    }
    .container .day-weather-information .temp{
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
    }
    .day-weather-information .temp h1{
        font-size: 3em;
    }
    .day-weather-information .temp p{
        font-size: 1em;
        top: 10%;
    }
    .day-weather-information .temp h1 span:nth-child(2){
        font-size: 0.5em;
        position: relative;
        right: 20px;
    }
    .day-weather-information .temp h1 span:nth-child(1){
        font-size: 0.8em;
        right: 15px;
        bottom: 15px;
    }
    .day-weather-information .more-info .wind span:nth-child(2),.day-weather-information .more-info .humidity span:nth-child(2){
        font-size: 1.2em;
     }
    .week-weather{
        grid-column: 2 / 3;
        grid-row: 1 / 3;
        flex-direction: column;
    }
    .line{
        height: 2px;
        width: 120px;
    }
    .container .weather-day{
        padding: 5px;
    }
    .container .weather-day .icon{
        height: 35px;
        width: 35px;
    }
    .container .weather-day h1.name-day{
        font-size: 18px;
    }
    .weather-day h1.day-temp{
        font-size: 18px;
    }
}
.day-weather-information{
    border-radius: 5px;
    overflow: hidden;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
}
.day-weather-information .temp{
    background-color:#111111a9;
    width:100%;
    height:75%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.temp h1{
    color: #fff;
    font-size: 7em;
    font-weight: 200;
}
.temp h1 span:nth-child(2){
    font-size: 0.6em;
    position: relative;
    right: 20px;
}
.temp h1 span:nth-child(1){
    font-size: 0.5em;
    position: relative;
    right: 35px;
    bottom: 40px;
}
.temp p{
    color: #fff;
    letter-spacing: 1px;
    font-size: 1.1em;
    position: relative;
    top: 10%;
}
.day-weather-information .more-info{
    background-color:#e48811a9;
    width:100%;
    height:25%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.more-info .wind, .more-info .humidity{
    width:50%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
}
.more-info .wind span img, .more-info .humidity span img{
    width: 60px;
}
.more-info .humidity span img{
    width: 70px;
    position: relative;
    top: 5px;
}
.more-info .wind span:nth-child(2),.more-info .humidity span:nth-child(2){
   font-size: 1.4em;
   font-weight: 700;
   color: #fff;
   letter-spacing: 1px;
   position: relative;
   right: 10px;
}

.city-information{
    border: 1px solid #ffffff2a;
    background: #ffffff21;
    border-radius: 5px;
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.weather-icon{
    display: block;
    width: 250px;
    height: 250px;
}
.city-long_haptitude{
    position: relative;
    bottom: 5%;
    font-size: 2em;
    width: 300px;
    display: flex;
    color: #fff;
    justify-content: space-around;
}
.city-long_haptitude span:nth-child(2){
   color: rgb(57, 166, 255);
}
.city-name{
    position: relative;
    bottom: 5%;
    font-size: 3.5em;
    letter-spacing: 2px;
    font-weight: 200;
    color: #fff;
    text-shadow: 0 0 5px #fff;
}

.weather-day{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 30px;
    color: #fff;
}

.weather-day:hover .icon{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.weather-day h1.name-day{
    font-weight: 200;
}
.weather-day h1.day-temp{
    font-weight: 300;
}
.weather-day .icon{
    height: 70px;
    width: 70px;
    display: block;
    transition: 0.3s ease-in-out;
}
.loading-content{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.loading-content img{
    display: inline-block;
    position: relative;
    top: 0;
    width:300px;
    -webkit-animation: loading 0.5s ease-in-out alternate infinite;
            animation: loading 0.5s ease-in-out alternate infinite;
}

@-webkit-keyframes loading{
    0%{
      top: -15px;
    }
    100%{
        top: 15px;
    }
}

@keyframes loading{
    0%{
      top: -15px;
    }
    100%{
        top: 15px;
    }
}
